export default function VistaEquipos() {
  const [equipo, setEquipo] = useState({
    nombre: "",
    nombrePais: "",
    nombrePersona: "",
  });
  const [err, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [nombre, setNombre] = useState(""); 
  const [equiposList, setEquiposList] = useState([]); 
  const [listEquipo, setListEquipo] = useState([]); 
  const [ciclistas, setCiclistas] = useState([]); 
  const [nuevoCiclista, setNuevoCiclista] = useState({
    nombre: "",
    fechaInicio: "",
    fechaFin: "",
  });
  const [searchCiclistaQuery, setSearchCiclistaQuery] = useState(""); 
  const [filteredCiclistas, setFilteredCiclistas] = useState([]); 
  const [formData, setFormData] = useState({
    nombre: "",
    añoEdicion: "",
    cantEtapas: "",
    kmTotales: "",
    idPersona: "",
  });

  // Obtener los ciclistas
  useEffect(() => {
    const fetchCiclistas = async () => {
      try {
        const res = await getCiclistas();
        setCiclistas(res);
        setFilteredCiclistas(res);
      } catch (error) {
        console.error("Error al obtener los ciclistas:", error);
        setError("No se pudieron cargar los ciclistas");
      }
    };
    fetchCiclistas();
  }, []);

  const handleEquipoSelect = (selectedEquipo) => {
    setEquipo({
      nombre: selectedEquipo.nombre,
      nombrePais: selectedEquipo.nombrePais,
      nombrePersona: selectedEquipo.nombrePersona,
    });
    setNombre(selectedEquipo.nombre); 
    setEquiposList([]); 

    useEffect(() => {
      const fetchEquipo = async () => {
        try {
          const Equipo = await getContratoEquipo();
          setListEquipo(Equipo); 
        } catch (error) {
          console.error("Error al obtener los Equipo:", error);
          setError("No se pudieron cargar los Equipo");
        }
      };
      fetchEquipo();
    }, []);
  };

  useEffect(() => {
    const equiposGuardados = localStorage.getItem("tablaEquipos");
    if (equiposGuardados) {
      setListEquipo(JSON.parse(equiposGuardados));
    }
  }, []);

  const handleApiSubmit = async () => {
    try {
      console.log("Enviando datos:", formData);
      setLoading(true);

      const response = await postPrueba(formData);

      console.log("Datos enviados con éxito:", response);
      alert("Prueba cargada con éxito");
    } catch (err) {
      console.error("Error al enviar los datos:", err);
      alert(`Ocurrió un error: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const confirm = window.confirm("¿Estás seguro de que deseas registrar este equipo?");
    if (confirm) {
      handleApiSubmit();
    }
  };

  // Función para manejar el cambio en el campo de búsqueda
  const handleSearchChange = (e) => {
    const value = e.target.value;
    setSearchCiclistaQuery(value);

    if (value.trim() !== "") {
      const filtered = ciclistas.filter((ciclista) =>
        `${ciclista.nombre} ${ciclista.apellido}`.toLowerCase().includes(value.toLowerCase())
      );
      setFilteredCiclistas(filtered);
    } else {
      setFilteredCiclistas(ciclistas);
    }
  };

  // Manejo de la selección del ciclista
  const handleSelectCiclista = (id, nombreCompleto) => {
    setNuevoCiclista({ ...nuevoCiclista, nombre: nombreCompleto });
    setSearchCiclistaQuery(""); 
    setFilteredCiclistas(ciclistas); 
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-50 p-12">
      <Link href="/" className="absolute top-4 left-4 text-xl text-blue-500 hover:underline">
        Volver al Inicio
      </Link>  
      <div className="bg-gray-100 p-12 rounded-xl shadow-2xl w-full max-w-6xl space-y-10">
        <h1 className="text-3xl font-bold text-black">Alta Prueba</h1>

        {/* Formulario */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
          {/* Año Edición */}
          <div className="col-span-1 sm:col-span-1">
            <label className="block text-lg font-medium mb-2 text-black">Año Edición</label>
            <input
              type="text"
              className="w-full p-4 border rounded-lg shadow-sm bg-white text-black outline-none"
              value={formData.añoEdicion}
              onChange={(e) => setFormData({ ...formData, añoEdicion: e.target.value })}
            />
          </div>

          {/* Nombre */}
          <div className="col-span-1 sm:col-span-1">
            <label className="block text-lg font-medium mb-2 text-black">Nombre</label>
            <input
              type="text"
              className="w-full p-4 border rounded-lg shadow-sm bg-white text-black outline-none"
              value={formData.nombre}
              onChange={(e) => setFormData({ ...formData, nombre: e.target.value })}
            />
          </div>

          {/* Kilómetros Totales */}
          <div className="col-span-1 sm:col-span-1">
            <label className="block text-lg font-medium mb-2 text-black">Kilómetros Totales</label>
            <input
              type="text"
              className="w-full p-4 border rounded-lg shadow-sm bg-white text-black outline-none"
              value={formData.kmTotales}
              onChange={(e) => setFormData({ ...formData, kmTotales: e.target.value })}
            />
          </div>

          {/* Cantidad Etapas */}
          <div className="col-span-1 sm:col-span-1">
            <label className="block text-lg font-medium mb-2 text-black">Cantidad Etapas</label>
            <input
              type="text"
              className="w-full p-4 border rounded-lg shadow-sm bg-white text-black outline-none"
              value={formData.cantEtapas}
              onChange={(e) => setFormData({ ...formData, cantEtapas: e.target.value })}
            />
          </div>

          {/* Ciclista Primero */}
          <div>
            <label className="block text-lg font-medium mb-3 text-black" htmlFor="director">
              Ciclista Primero
            </label>
            <div className="flex items-center border rounded-lg shadow-sm bg-white">
              <select
                id="idPersona"
                name="idPersona"
                value={formData.idPersona}
                onChange={(e) => setFormData({ ...formData, idPersona: e.target.value })}
                className="flex-1 px-4 py-3 outline-none text-black"
              >
                <option value="">Buscar Ciclista</option>
                {ciclistas.map((dir) => (
                  <option key={dir.id} value={dir.id}>
                    {dir.nombre} {dir.apellido}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        {/* Botón Agregar Ciclista */}
        <div className="flex justify-end p-8">
          <button
            onClick={handleSubmit}
            className="group/button relative inline-flex items-center justify-center overflow-hidden rounded-md bg-green-600 backdrop-blur-lg px-6 py-2 text-base font-semibold text-white transition-all duration-300 ease-in-out hover:scale-110 hover:shadow-xl hover:shadow-gray-600/50 border border-white/20"
          >
            Agregar Equipo
          </button>
        </div>

        {/* Tabla de Equipo */}
        <div className="overflow-x-auto">
          <table className="w-full border-collapse border border-gray-300 text-black">
            <thead>
              <tr className="bg-gray-200">
                <th className="border border-gray-300 p-4 text-center font-semibold">Número</th>
                <th className="border border-gray-300 p-4 text-center font-semibold">Equipo</th>
              </tr>
            </thead>
            <tbody>
              {listEquipo.map((equipo) => (
                <tr key={equipo.id}>
                  <td className="border border-gray-300 p-4 text-center">{equipo.id}</td>
                  <td className="border border-gray-300 p-4 text-center">{equipo.nombre}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
